.bulb-video {
  grid-template-columns: repeat(4, 1fr);
  max-width: 90%;
  margin: 0 auto;
}
@media screen and (min-width: 960px) {
  .bulb-video {
    display: grid;
    max-width: 1500px;
  }
}

.bulb-video-caption {
  display: flex;
  margin-bottom: 1em;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
}

.bulb-video-caption-timestamp:hover {
  cursor: pointer;
}

.bulb-video-player {
  background: gray;
  width: 100%;
}
@media screen and (min-width: 960px) {
  .bulb-video-player {
    grid-column: span 2;
  }
}
@media screen and (min-width: 1400px) {
  .bulb-video-player {
    grid-column: span 3;
  }
}

.bulb-video-captions-container {
  background: #fff;
  grid-column: span 2;
  height: 0;
  min-height: 0;
  overflow-y: auto;
  padding: 30px;
  padding-bottom: 51%;
}
@media screen and (min-width: 1400px) {
  .bulb-video-captions-container {
    grid-column: span 1;
    padding-bottom: 161%;
  }
}

.bulb-video-caption-timestamp,
.bulb-video-caption p {
  padding: 0.2em 0.5em;
  margin: 0;
}

.bulb-video-caption-timestamp {
  align-self: start;
  background-color: #28303d;
  border-radius: 5px;
  color: #fff;
  font-size: 13px;
  font-weight: 700;
  opacity: 0.75;
  padding: 0.1em 0.5em;
}
.bulb-video-caption-timestamp:hover {
  opacity: 1;
}

.bulb-video-caption p {
  line-height: 1.3;
}

.bulb-video-player {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  height: 0;
}

.bulb-video-player iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.bulb-pagination {
  display: flex;
  font-size: 0.8em;
  margin-top: 0.8em;
  margin-bottom: 0.8em;
}
.bulb-pagination a, .bulb-pagination span, .bulb-pagination .bulb-pagination-label {
  margin: 2px 2px 2px 0;
  padding: 6px 9px 5px 9px;
}
.bulb-pagination-label {
  font-weight: bold;
}
.bulb-pagination a, .bulb-pagination span {
  text-decoration: none;
  width: auto;
  color: #222;
  /* Pagination text color */
  background: #c0c0c0;
  /* Pagination non-active background color */
  transition: background 0.15s ease-in-out;
}
.bulb-pagination a:hover {
  color: #f4f4f4;
  background: #6aac70;
  /* Pagination background on hover */
}
.bulb-pagination .current {
  padding: 6px 9px 5px 9px;
  background: #626e81;
  /* Current page background */
  color: #f4f4f4;
}

.bulb-learning-module .single-meta, .bulb-learning-module .archive-link-container {
  display: none;
}
.bulb-learning-module input[type=checkbox], .bulb-learning-module input[type=radio] {
  position: absolute;
  width: 100%;
  height: 100%;
}
.bulb-learning-module input[type=checkbox]:disabled, .bulb-learning-module input[type=radio]:disabled {
  opacity: 0;
}

.bulb-archive-container {
  margin-left: auto;
  margin-right: auto;
  width: 80%;
}

.bulb-list {
  list-style-type: none;
}
.bulb-list-item {
  padding-left: 2%;
}