.answer-list {
  list-style-type: none;
  padding-left: 2.5em;
  /* material-ui draws native inputs and hides them when disabled.
  some themes may have their own conflicting styles, so we enforce
  hidden disabled inputs here to prevent ghost controls showing up. */
}
.answer-list input:disabled {
  opacity: 0;
}
.answer-list .answer, .answer-list .answer-incorrect, .answer-list .answer-correct {
  border: solid 0.2em;
  border-color: rgba(255, 255, 255, 0);
  border-radius: 0.8em;
  box-sizing: border-box;
  margin: 0.1em;
}
.answer-list .answer-correct {
  border-color: #4bad38;
}
.answer-list .answer-incorrect {
  border-color: #aa3232;
}
.answer-list .feedback-icon, .answer-list .feedback-icon-incorrect, .answer-list .feedback-icon-correct {
  font-size: 2em;
  left: auto;
  margin-left: -1.05em;
  margin-top: 0.36em;
  position: absolute;
  top: auto;
}
.answer-list .feedback-icon-correct {
  color: #4bad38;
}
.answer-list .feedback-icon-incorrect {
  color: #aa3232;
}
.answer-list .feedback {
  font-style: italic;
  margin-left: 3em;
}